.main_container {
  position: relative;
  max-width: 1096px;
  margin-left: auto;
  margin-right: auto;
  background-color: #F0F0F0;
  border-radius: 34px;
  padding-top: 30px;
  padding-left: 44px;
  padding-right: 44px;
  padding-bottom: 30px;
  height: 100%;
}

.vision_tag {
  position: absolute;
  top: 0px;
  left: 0px;
  padding: 14px 52px;
  background: linear-gradient(180deg, #35A483 0%, #077854 100%);
  border-radius: 34px 0px;
  p {
    color: #fff;
    font-size: 24px;
    line-height: 35px;
  }
}

.content_box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.col_l {
  h1 {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 50px;
    line-height: 72px;
    background: -webkit-linear-gradient(#35A483 ,#077854);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  p {
    font-size: 18px;
    line-height: 26px;
    color: #424242;
    font-weight: 400;
  }
}

.col_r {
  position: relative;
  max-width: 417px;
  border: 2px dashed #B7B7B7;
  border-radius: 26px;
  background: #FFFFFF;
  text-align: center;
  padding: 30px 95px 90px 95px;
  margin-right: 70px;
  img {
    width: 126px;
    height: 102px;
    margin-bottom: 20px;
  }
  h3 {
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 23px;
    color: #424242;
    font-weight: 400;
  }
  p {
    font-size: 14px;
    line-height: 20px;
    color: #077854;
  }
  &:before {
    content: '';
    display: block;
    width: 145px;
    height: 278px;
    position: absolute;
    right: -67px;
    bottom: -5px;
    background-image: url(#{$imgs}img_man.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
  &:after {
    content: '';
    display: block;
    width: 670px;
    height: 318px;
    position: absolute;
    right: -113px;
    bottom: -180px;
    background-image: url(#{$imgs}img_people.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
  
}