@import "./variable";
@import "./reset";
@import "./mixin";
@import "./index";
@import "./inputs";

body.layout_section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

header {
  padding-top: 28px;
  padding-left: 40px;
  padding-right: 40px;
  a.logo {
    display: block;
    width: 89px;
    height: 57px;
    background-image: url(#{$imgs}logo.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
}

.page_section {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  padding-top: 19px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 20px;
}

.page_section:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  width: 122px;
  height: 255px;
  background-image: url(#{$imgs}bg_top.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top right;
}

.page_section:after {
  content: '';
  display: block;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 243px;
  height: 305px;
  background-image: url(#{$imgs}bg_bottom.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top right;
}

footer {
  width: 100%;
  text-align: right;
  p {
    font-size: 12px;
    line-height: 17px;
    color: #424242;
    padding-bottom: 18px;
    padding-right: 18px;
  }
}