// index_upload
.custom-file.upload_file {
  display: block;
  margin-bottom: 15px;
  width: 227px;
  height: 60px;
}
.custom-file-input.upload_file {
  width: 227px;
  height: 60px;
}
.custom-file-label.upload_file {
  left: 50%;
  transform: translateX(-50%);
  width: 227px;
  height: 60px;
  background: linear-gradient(180deg, #35A483 0%, #077854 100%);
  box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.11);
  border-radius: 16px;
  color: #fff;
  font-size: 18px;
  line-height: 26px;
  padding-top: 17px;
  padding-bottom: 17px;
  &:after {
    display: none;
  }
}