body {
  margin: 0;
  font-family: $font-family;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
li {
  margin: 0;
}

a {
  transition: all .3s;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}